/**
 * Template for isgi
 */
 <i18n>
{
   "en":{
      "server_hs":"The service do not respond.<br />Please retry later."
   },
   "fr":{
      "server_hs": "Les services ne rpondent pas. <br />Veuillez rssayez ultrieurement."
   }
}
</i18n>
<template>
<span class="isgi-error" v-show="visible">
  <div v-html="$t('server_hs')">


  </div>
</span>
</template>
<script>
export default {
	 props:{
	        lang: {
	            type:String,
	            default:'fr'
	        }
    },
    data(){
        return{
        	error:null,
        	errorRequestListener:null,
        	visible:false
        }
    },
	
	
	methods:{
		handleError( e ){
			this.error = e.detail.error;
			this.visible = true;
		}
		
    },
    created(){
        this.$i18n.locale = this.lang;    
        this.errorRequestListener = this.handleError.bind( this);
        document.addEventListener('errorSearchIndiceEvent', this.errorRequestListener); 
    },
    destroyed(){
    	  document.removeEventListener('errorSearchIndiceEvent', this.handleError);
          this.handleError =null
    }
}
</script>
<style>
.isgi-error{
	margin-left: 15px;
	margin: 5px 5px 5px 10px;
	padding: 10px;
	box-sizing: border-box;
	position: relative;
	display: block;
	color: darkred;
	font-size: 1.3em;
	text-align:center;
	transition: transform 4s ease-out;
	border:5px solid darkred;
	box-shadow: 0 2px 5px rgba(0, 0, 0, 0.2);
}
</style>


